'use client';

import { useEffect, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { doc, getDoc } from 'firebase/firestore';
import { auth, db } from '@/lib/firebaseClient';
import { useAuth } from '@/lib/auth-context';
import { Button } from '@/components/ui/button';
import { AuthInput } from '@/components/ui/auth-input';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function PlayerLoginPage() {
  const [identifier, setIdentifier] = useState(''); // Email
  const [password, setPassword] = useState('');
  const [isPasswordLogin, setIsPasswordLogin] = useState(false); // Toggle for password mode
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const router = useRouter();
  const { user, loading: authLoading } = useAuth();
  const searchParams = useSearchParams();
  const [redirectTo, setRedirectTo] = useState('/');

  // Preserved redirect logic
  useEffect(() => {
    const nextParam = searchParams?.get('next');
    let target = nextParam || '/';

    if (!nextParam && typeof window !== 'undefined') {
      const referrer = document.referrer;
      if (referrer) {
        try {
          const url = new URL(referrer);
          if (url.origin === window.location.origin) {
            target = `${url.pathname}${url.search}${url.hash}` || '/';
          }
        } catch {
          target = '/';
        }
      }
    }

    if (target.startsWith('/players/login') || target.startsWith('/players/signup')) {
      target = '/';
    }

    setRedirectTo(target);
  }, [searchParams]);

  // Handle redirects ONLY after we know the user is authenticated and have checked their profile
  const handleAuthRedirect = async (uid: string) => {
    try {
      // Check onboarding status
      const userDoc = await getDoc(doc(db, 'players', uid));
      const userData = userDoc.data();
      const onboardingCompleted = userData?.onboardingCompleted === true;

      if (!onboardingCompleted) {
        router.push('/players/onboarding/personal-details');
        return;
      }
      
      // If onboarding is done, go to intended destination
      router.push(redirectTo || '/');
    } catch (err) {
      console.error('Error checking onboarding status:', err);
      // Fallback to home
      router.push('/');
    }
  };

  // Watch for Auth Context changes (e.g. if they are already logged in)
  useEffect(() => {
    if (!authLoading && user) {
       handleAuthRedirect(user.uid);
    }
  }, [authLoading, user, redirectTo]); // Removed handleAuthRedirect from deps to avoid loop

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    if (isPasswordLogin) {
      // Standard Email/Password Login
      try {
        const cred = await signInWithEmailAndPassword(auth, identifier, password);
        // The useEffect will handle the redirect once 'user' updates
        // But for faster feedback, we can call it here too
        await handleAuthRedirect(cred.user.uid);
      } catch (err: any) {
        console.error(err);
        setError('Invalid email or password.');
        setLoading(false);
      }
    } else {
      // Magic Link / OTP flow (Not fully implemented on backend yet as per constraints)
      // For now, simulate or ask for password
      setLoading(false);
      setError('Passwordless login is coming soon. Please use your password.');
      setIsPasswordLogin(true); // Switch them to password mode automatically
    }
  };

  if (authLoading) return null; // Or a loading spinner

  return (
    <div className="min-h-screen grid items-center justify-center bg-background p-4">
      <div className="w-full max-w-sm space-y-8 animate-in fade-in zoom-in duration-500">
        
        <div className="text-center space-y-2">
          <h1 className="text-3xl font-bold tracking-tight">Log in to play</h1>
          <p className="text-lg text-muted-foreground">
            Book courts, find matches, and play more.
          </p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-4">
            <AuthInput
              type="email"
              placeholder="Email address"
              value={identifier}
              onChange={(e) => setIdentifier(e.target.value)}
              required
              className="bg-muted/30 border-muted-foreground/20 focus:bg-background"
            />
            
            {isPasswordLogin && (
               <AuthInput
               type="password"
               placeholder="Password"
               value={password}
               onChange={(e) => setPassword(e.target.value)}
               required
               className="bg-muted/30 border-muted-foreground/20 focus:bg-background animate-in slide-in-from-top-2"
             />
            )}
          </div>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <Button 
            type="submit" 
            className="w-full h-14 text-lg rounded-2xl font-semibold shadow-lg shadow-primary/20"
            disabled={loading || !identifier}
          >
            {loading ? 'Logging in...' : 'Continue'}
          </Button>

          {!isPasswordLogin ? (
               <button 
               type="button"
               onClick={() => setIsPasswordLogin(true)}
               className="w-full text-center text-sm text-primary hover:underline"
             >
               I have a password
             </button>
          ) : (
            <p className="text-center text-sm text-muted-foreground">
              Weâ€™ll send you a secure code to log in.
            </p>
          )}
        </form>
      </div>
    </div>
  );
}
